.section {
  @include full-flex;
  margin-bottom: 3rem;
  position: relative;

  // &:last-of-type {
  //   .section__content {
  //     padding-bottom: 0;
  //   }
  // }
}

.section__content {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}

.section__table-wrapper {
  border-bottom-left-radius: $section-border-radius;
  border-bottom-right-radius: $section-border-radius;
  box-shadow: 0 2px 2px 0 rgba($c-mine-shaft, 0.14), 0 3px 1px -2px rgba($c-mine-shaft, 0.2),
    0 1px 5px 0 rgba($c-mine-shaft, 0.12);
  overflow: hidden;
  position: relative;
  transition: height $transition-timing ease-in-out;
  width: 100%;
  box-shadow: $card-shadow;
}

@each $dynamic-color-class, $dynamic-color in (color1, $color1), (color2, $color2), (color3, $color3), (color4, $color4),
  (color5, $color5)
{
  .section--#{$dynamic-color-class} {
    .race-information__race-info-inner {
      > p {
        color: $c-white;
      }
    }

    .section-table {
      background-color: rgba(#272727, 0.25);
      // background-color: darken(rgba($dynamic-color, 0.1), 10%);
      // filter: grayscale(0.9);
      // background-color: lighten(rgba($c-black, 0.5), 5%);
      // background-color: rgba($dynamic-color, 0.166);
    }

    .section-content__icon {
      @include remove-highlight;
    }

    .section-content__icon {
      &:focus {
        .section-content__icon-text {
          color: lighten($dynamic-color, 12.75%);
        }

        svg {
          fill: lighten($dynamic-color, 35%);
        }
      }

      &:focus,
      &:hover {
        .section-content__icon-text {
          color: lighten($dynamic-color, 25%);
        }

        svg {
          fill: lighten($dynamic-color, 25%);
        }
      }

      &.active {
        .section-content__icon-text {
          color: lighten($dynamic-color, 15%);
        }

        svg {
          fill: lighten($dynamic-color, 15%);
        }
      }
    }

    .section-table__sortable {
      color: lighten($dynamic-color, 15%);
    }

    .section-content__icon {
      // background-color: rgba(darken($dynamic-color, 50%), 0.2);
      // background-color: darken(rgba($dynamic-color, 0.33), 30%);
      // filter: grayscale(0.1);
    }

    .mini-sub-heading,
    .section-table th,
    .section-table th button {
      color: $c-white;

      &.section-table__sortable {
        color: lighten($dynamic-color, 15%);
      }
    }

    .section-table__sortable::before,
    .section-table__sortable::after {
      border-color: lighten($dynamic-color, 20%) transparent transparent transparent;
    }
  }
}

.section-content__header {
  align-items: flex-end;
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 100%;
  flex-direction: column;
  border-top-left-radius: $card-border-radius;
  border-top-right-radius: $card-border-radius;

  @include bp-xs {
    flex-direction: row;
  }
}

.section-content__heading {
  color: $c-white;
  letter-spacing: 1px;
  margin-bottom: 0.1rem;
}

.section-content__icons {
  align-items: center;
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-left: auto;
  // overflow: hidden;
}

.section-content__icon {
  @include font-size(13px);
  @include transition(transform, 0.15s, $ease-in-out-quad);
  background-color: rgba(#272727, 0.7);

  align-items: center;
  cursor: pointer;
  display: flex;
  font-family: $f-titillium-normal;
  padding-left: 0.6rem;
  padding-right: 0.6rem;
  position: relative;
  text-align: center;
  padding-bottom: 0.25rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.3rem;
  margin-left: 0.33rem;
  border-top-left-radius: $card-border-radius;
  border-top-right-radius: $card-border-radius;
  // border-top: 1px solid rgba($c-cod-grey, 0.9);
  // border-left: 1px solid rgba($c-cod-grey, 0.9);
  // border-right: 1px solid rgba($c-cod-grey, 0.9);
  // transform: translate3d(0, 0.0625rem, 0);
  svg {
    @include transition(fill, 0.15s, $ease-in-out-quad);
    fill: rgba($c-white, 1);
    height: auto;
    width: 0.8rem;
    margin-right: 0.5rem;
    // display: none;
  }

  &.active {
    // transform: translate3d(0, 0, 0);
  }
}

.section-content__icon-text {
  @include transition(color, 0.15s, $ease-in-out-quad);
  @include font-size(13px);
  color: rgba($c-white, 0.666);
  font-family: $f-titillium-normal;
  position: relative;
  // transform: translateZ(0.1px);
}

.section-content__icon--previous-result {
  transition: opacity 0.15s ease-in;
  &.inactive {
    opacity:0;
  }
}
