.site-footer {
  @include full-flex();
  align-items: center;
  align-self: flex-end;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.site-footer__cite {
  text-align: left;
}

.site-footer__info {
  text-align: right;
}

.site-footer__cite,
.site-footer__info {
  @include font-size(13px, 13px);
  color: rgba($c-white, 0.5);

  a {
    color: rgba($c-white, 0.5);
    text-decoration: underline;
  }
}
