$c-black: #000;
$c-white: #fff;
$c-mine-shaft: #2c2c2c;
$c-grey: #212121;
$c-cod-grey: #212121;
$c-cod-grey--lighten: lighten($c-cod-grey, 2%);
$c-grey-lightened--inverted: invert($c-cod-grey--lighten);

// From Coolors.co
$color1: #ed1c24;
