$color1-dark: darken($color1, 10%);
$color2-dark: darken($color2, 10%);
$color3-dark: darken($color3, 10%);
$color4-dark: darken($color4, 10%);
$color5-dark: darken($color5, 10%);

// Fallback F1 team colours
$c-british-racing-green: #004225;
$c-bleu-de-france: #318ce7;
$c-rossa-corsa-red: #d40000;
$c-imperial-blue: #002395;

// 2019 F1 Team colours
$c-mercedes: #00d2be;
$c-ferrari: #dc0000;
$c-red-bull: #1e41ff;
$c-renault: #fff500;
$c-haas-f1-team: #f0d787;
$c-racing-point: #f596c8;
$c-toro-rosso: #c8c8c8; // 2021 updated
$c-mclaren: #ff8700;
$c-alfa-romeo: #9b0000;
$c-williams: #fff;
$c-aston-martin: #02655c; // 2021 new

// Misc F1 team colours
$c-silver: #c0c0c0;
$c-silver-2: #c8c8c8;
$c-red: #f00;
$c-green: #008000;
$c-orange: #ffa500;
$c-purple: #800080;
$c-yellow: #ff0;
$c-blue: #00f;
$c-brown: #a52a2a;
$c-brp: #a7c682;
$c-theodore: #000080;
$c-wolf: #00008b;
$c-march-brm: #afeeee;
$c-caterham: #90ee90;
$c-leyton-house: #5ba07a;
$c-lotus: #9b8250;

// Material
$c-pomegranate: #f44336;
$c-fruit-salad: #4caf50;

// Z-index
$z-race-information__inner: 1;
$z-race-select: 2;
$z-race-header__top: 3;
$z-race-information__circuit: 4;

// Fonts
$f-titillium-light: 'Titillium Light';
$f-titillium-normal: 'Titillium Normal';
$f-titillium-semi-bold: 'Titillium Semi-Bold';

// Misc
$section-border-radius: 5px;
$transition-timing: 0.15s;
$container-max-width: 33rem;
$container-max-width-md: 36rem;
$show-seasons-height: 6.5rem;
$card-shadow: 0rem 0.1875rem 0.375rem rgba($c-black, 0);
$card-border-radius: 5px;

// Easings
$ease-in-quad: cubic-bezier(0.55, 0.085, 0.68, 0.53);
$ease-in-cubic: cubic-bezier(0.55, 0.055, 0.675, 0.19);
$ease-in-quart: cubic-bezier(0.895, 0.03, 0.685, 0.22);
$ease-in-quint: cubic-bezier(0.755, 0.05, 0.855, 0.06);
$ease-in-expo: cubic-bezier(0.95, 0.05, 0.795, 0.035);
$ease-in-circ: cubic-bezier(0.6, 0.04, 0.98, 0.335);

$ease-out-quad: cubic-bezier(0.25, 0.46, 0.45, 0.94);
$ease-out-cubic: cubic-bezier(0.215, 0.61, 0.355, 1);
$ease-out-quart: cubic-bezier(0.165, 0.84, 0.44, 1);
$ease-out-quint: cubic-bezier(0.23, 1, 0.32, 1);
$ease-out-expo: cubic-bezier(0.19, 1, 0.22, 1);
$ease-out-circ: cubic-bezier(0.075, 0.82, 0.165, 1);

$ease-in-out-quad: cubic-bezier(0.455, 0.03, 0.515, 0.955);
$ease-in-out-cubic: cubic-bezier(0.645, 0.045, 0.355, 1);
$ease-in-out-quart: cubic-bezier(0.77, 0, 0.175, 1);
$ease-in-out-quint: cubic-bezier(0.86, 0, 0.07, 1);
$ease-in-out-expo: cubic-bezier(1, 0, 0, 1);
$ease-in-out-circ: cubic-bezier(0.785, 0.135, 0.15, 0.86);
