.race-information__inner {
  @include full-flex;
  border-radius: $section-border-radius;
  z-index: $z-race-information__inner;
  // border-width: 2px;
  // border-right-style: solid;
  // border-left-style: solid;
  // border-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba($color1, 0) 15%, rgba($color1, 0.5) 100%) 0 100%;
}

.race-information__wrap {
  @include full-flex;
  box-shadow: $card-shadow;
  background-color: rgba(#272727, 0.7);
  // border-bottom-right-radius: $card-border-radius;
  // border-bottom-left-radius: $card-border-radius;
  overflow: hidden;
  // border-bottom-style: solid;
  // border-bottom: 3px rgba($color1, 0.5) solid;
  // border-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba($color1, 0) 15%, rgba($color1, 1) 100%) 0 100%;
  // border-width: 2px;
  // &.overflow-active {
  overflow: visible;
  // }
}

.race-information__race-info {
  justify-content: center;
  min-height: 7rem;
  padding-bottom: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 1rem;

  strong {
    // color: rgba(lighten($color1, 30%), 0.9);
    color: $c-white;
    font-family: $f-titillium-semi-bold;
  }
}

.race-information__race-info-inner {
  @include font-size(14px);
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
  width: 100%;

  > p {
    align-self: flex-start;
    margin-bottom: 0.4em;
    position: relative;
    z-index: 2;

    &:last-of-type {
      margin-bottom: 0;
    }

    > span {
      color: rgba(lighten($color1, 12.75%), 1);
      // color: $color1;
      font-family: $f-titillium-semi-bold;
      // #f36368
      margin-left: 0.2rem;
      position: relative;
      z-index: 1;
    }
  }
}

.race-information__weather-icon-svg {
  align-self: flex-end;
  max-height: 4.5rem;
  max-width: 25%;
  opacity: 0.75;
  position: absolute;
  right: 1rem;
  top: 1rem;
}

.map-wrapper,
.race-information__circuit {
  filter: sepia(0.1) grayscale(1) invert(0.9);
  height: 18rem;

  &.filter-inactive {
    filter: none;
    .vue2leaflet-map {
      mix-blend-mode: initial;
    }
  }
}

.map-wrapper {
  @include transition(filter, 0.4s, $ease-out-expo);
  height: 18rem;
  overflow: hidden;
}

.race-information__circuit {
  align-items: center;
  // background-color: rgba($c-grey-lightened--inverted, 0.95);
  display: flex;
  filter: invert(1);
  justify-content: center;

  .race-information__circuit-inner {
    display: flex;
    position: relative;
    width: 100%;
  }
}

.race-information__circuit-img-wrap {
  align-items: center;
  border-bottom-left-radius: $card-border-radius;
  border-bottom-right-radius: $card-border-radius;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  outline: none;
  position: relative;
  width: 100%;
}

.race-information__circuit-img {
  align-self: center;
  background-color: rgba($c-grey-lightened--inverted, 0.65);
  border-radius: $section-border-radius;
  max-height: 100%;
  object-fit: contain;
  outline: none;
  padding-bottom: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 1rem;
}

.race-information__zoom-icon {
  bottom: 0.5rem;
  display: block;
  opacity: 0.75;
  position: absolute;
  right: 0.5rem;
  width: 1.25rem;
}

.vue2leaflet-map {
  height: 100%;
  mix-blend-mode: hard-light;
  width: 100%;
}

.race-information__map-colour-btn {
  background-color: $c-white;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  cursor: pointer;
  height: 30px;
  left: 10px;
  padding: 3px;
  position: absolute;
  top: 71px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  mix-blend-mode: initial;

  svg {
    display: block;
    height: calc(100% - 0px);
    width: calc(100% - 0px);
  }
}
